import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import Set from '@/components/set'
import N_set from '@/components/n_set'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },{
      path: '/n_set',
      name: 'N_set',
      component: N_set
    },{
      path: '/set',
      name: 'Set',
      component: Set
    }
  ]
})



