<template>
    <div class="box">
       <img src="../assets/e167d330097d60b3696521e92f964293.jpg" alt="">
        <div v-show="!is_list" @click="see_list" class="see_list">展开</div>
        <div class="list_box">
            <div class="list" v-show="is_list">
                <div class="show_list" @click="show_list">收起</div>
                <div class="add_list">
                    <div><input type="text" @keypress="key_add" @focus="check_focus" @input="check_input" v-model="input_name" placeholder="输入员工姓名"><button @click="add_name">增加</button></div>
                    <p style="height:30px;font-size:14px;color:red;">{{p}}</p>
                </div>
                <div class="name_list">
                    <h4>公司员工列表 <span>({{name_list.length}}人)</span></h4>
                    <ul>
                        <li v-for="(item,index) in name_list" :key="index">{{item}}<span @click="remove_name(index)">删除</span></li>
                    </ul>
                    <span><router-link to="/n_set" target="_blank">设置</router-link></span>
                </div>
            </div>
        </div>
        <div class="game_list" ref="game_list">
            <div class="list_title">{{set_title}}</div>
            <div class="now_price">现在在抽取:{{price_name}}</div>
            <div class="game_box">
                <div>
                    <span class="choice_now_price">选择当前奖项</span>
                    <ul>
                        <li @click="set_now_price(item.name,item.num)" v-for="(item,index) in price_list" :key="index">{{item.name}}</li>
                    </ul>
                </div>
                <div>
                    <div ref="name_box">{{game_name}}</div>
                    <div @click="start_game" v-if="is_start==1">开始</div>
                    <div v-else @click="stop_game">停止</div>
                    </div>
                <div>
                    <p>中奖名单</p>
                    <ul>
                        <li v-for="(item,index) in get_price" :key="index"><span>{{item.price_name}}</span>：<span>{{item.name}}</span></li>
                    </ul>
                </div>
            </div>
            <div class="kongtou"><img src="../assets/cb9f1a1cd40bde1f317034069ae31c17.png" alt=""></div>
        </div>
    </div>
</template>
<script>
export default {
  name: 'HelloWorld',
    data () {
        return {
            name_list: [],
            input_name:'',
            p1:"已存在相同名字，如果同名请加符号区分！",
            p2:'请先输入员工名字！',
            p:'',
            game_name:"",
            w:'',
            is_start:1,
            game_num:0,
            is_list:false,
            price_num:'',
            price_name:"",
            get_price:[],
            set_title:'普拉多进出口4S店2018年会抽奖'
        }
    },
    mounted(){
        var arry=localStorage.getItem("name");
        if(arry==""){
            this.name_list=[];
        }else{
            this.name_list=arry.split(",");
        }
        var num=Math.floor(Math.random()*this.name_list.length)
        this.game_name=this.name_list[num];
        this.price_name=this.price_list[0].name;
        var title=JSON.parse(localStorage.getItem("title"));
        title==null?this.set_title:this.set_title=title;
    },
    computed:{
        name_num:{
            get: function () {
              return Math.floor(Math.random()*this.name_list.length)
            },
            set: function (newValue) {
//                console.log(newValue)
                return newValue
            }
        },
        price_list:{
            get: function () {
              return JSON.parse(localStorage.getItem("n_set"))
            },
            set: function (newValue) {
//                console.log(newValue)
                return newValue
            }
        }
    },
    methods:{
        add_name(){
            for(var i of this.name_list){
                if(i==this.input_name){
                    this.p=this.p1;
                    return;
                }
            }
            if(this.input_name!=""){
                this.name_list.push(this.input_name);
                localStorage.setItem('name',this.name_list)
            }else{
                this.p=this.p2;
            }
            this.input_name=""
            
        },
        set_now_price(name,num){
            this.price_num=num;
            this.price_name=name;
//            var name_box=this.$refs.name_box;
//            for(var i=0;i<this.price_num;i++){
//                name_box.innerHTML+='<span></span>'
//            }
        },
        remove_name(index){
            this.name_list.splice(index,1);
            localStorage.setItem('name',this.name_list)
        },
        check_input(){
            this.input_name==''?this.p='':this.p=this.p;
        },
        key_add(e){
            if(e.keyCode==13){
              this.add_name();
            }
        },
        check_focus(){
            this.input_name==''?this.p='':this.p=this.p;
        },
        start_game(){
            this.is_start=3;
            this.get_num()
            
        },
        get_num(){
            clearInterval(this.w);
            var dd=this.name_list
            var el=this;
            var d;
            this.w=setInterval(function(){
                d=(Math.floor(Math.random()*dd.length));
                    el.game_name=dd[d];
            },100)
            this.game_num++;
        },
        stop_game(){
            var el=this;
            clearInterval(this.w)
            setTimeout(function(){
                el.is_start=1;
            },999)
            var set_num=JSON.parse(localStorage.getItem("set"))
            for(var i of set_num){
                if(this.game_num==i.num){
                    i.name!=null?this.game_name=i.name:this.game_name=this.game_name;
                }
            }
            this.get_price.push({
                price_name:this.price_name,
                name:this.game_name
            })
        },
        show_list(){//隐藏列表
            this.is_list=!this.is_list;
            
        },
        see_list(){//显示列表
            this.is_list=!this.is_list;
        }
    }
}
</script>

<style scoped>
    .box{
        position: relative;
/*        background: url(/static/img/e167d330097d60b3696521e92f964293.jpg) no-repeat;*/
/*        background-size: 100%;*/
        height: 600px;
        padding-top: 50px;
        background-color: #6f1010;
    }
    .box>img{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
    .list_box{
        position: fixed;
        z-index: 99;
        top: 40px;
        left: 0;
    }
    .list{
        border: 1px solid gray;
        width: 300px;
        height: 600px;
        float: left;
        position: relative;
        background-color: antiquewhite;
    }
    .show_list{
        position: absolute;
        top: -30px;
        right: 0;
        width: 50px;
        height: 30px;
        line-height: 30px;
        background-color: darkgray;
        cursor: pointer;
        color: white;
    }
    .see_list{
        position: fixed;
        width: 50px;
        height: 30px;
        line-height: 30px;
        background-color: darkgray;
        cursor: pointer;
        color: white;
        top: 30px;
        left: 0;
        z-index: 100;
    }
    .add_list{
        margin-top: 30px;
    }
    .add_list>div{
        width: 255px;
        height: 31px;
        border: 1px solid gray;
        margin: auto;
        margin-bottom: 10px;
    }
    .add_list>div>input{
        outline: none;
        height: 30px;
        width: 200px;
        padding-left: 5px;
        font-size: 16px;
        border: 0;
    }
    .add_list>div>button{
        outline: none;
        border: 0;
        height: 31px;
        width: 50px;
        color: white;
        background-color: darkgreen;
        cursor: pointer;
    }
    .name_list>h4{
        height: 40px;
        width: 100%;
        background-color: beige;
        line-height: 40px;
    }
    .name_list>ul{
        height: 400px;
        width: 100%;
        overflow: auto;
    }
    .name_list>ul>li{
/*        width: 98%;*/
        margin: auto;
        height: 40px;
        line-height: 40px;
        font-size: 16px;
        cursor: pointer;
        text-align: left;
        padding-left: 30px;
    }
    .name_list>ul>li:hover{
        background-color: gainsboro;
    }
    .name_list>ul>li>span{
        float: right;
        margin-right: 30px;
        color: dodgerblue;
    }
    .name_list>ul>li>span:hover{
        text-decoration: underline;
    }
    .list_title{
        font-size: 50px;
    }
    .now_price{
        font-size: 16px;
        margin: 100px 0 20px 230px;
        text-align: left;
        color: white;
    }
    .game_list{
        float: left;
        width: 100%;
        height: 500px;
        position: relative;
    }
    .game_box{
        width: 100%;
        margin: auto;
        position: relative;
    }
    .game_box>div{
        float: left;
    }
    .game_box>div:nth-child(1){
        width: 100px;
        margin-right: 80px;
        margin-left: 50px;
    }
    .game_box>div:nth-child(1)>ul>li{
        height: 30px;
        background-color: rgba(111, 16, 16, 0.67);
        color: white;
        margin: 5px 0;
        line-height: 30px;
        cursor: pointer;
    }
    .game_box>div:nth-child(1)>ul>li:hover{
        background-color: rgba(128, 128, 128, 0.49);
    }
    .game_box>div:nth-child(2)>div{
        float: left;
    }
    .game_box>div:nth-child(2)>div:nth-child(1){
        width: 500px;
        height: 250px;
/*        border: 13px solid yellow;*/
        font-size: 70px;
        color: red;
        line-height: 250px;
        border-radius: 20px;
        background-color: #eec536;
/*        color: white;*/
    }
    .game_box>div:nth-child(2)>div:nth-child(2){
        width: 150px;
        height: 60px;
        line-height: 60px;
/*        border: 1px solid gray;*/
        cursor: pointer;
        border-radius: 20px;
        font-size: 20px;
        color: white;
        margin: 100PX 0 0 100PX;
        background-color: #7a1618;
    }
    .game_box>div:nth-child(3){
        position: absolute;
        right: 80px;
        top: -30px;
        background-color: rgba(111, 16, 16, 0.67);
        padding: 7px;
        width: 150px;
        height: 300px;
        overflow: auto;
    }
    .game_box>div:nth-child(3)>p{
        color: yellow;
        font-size: 18px;
        line-height: 30px;
    }
    .game_box>div:nth-child(3)>ul>li{
        line-height: 30px;
        background-color: rgba(128, 128, 128, 0.4);
        color: white;
        text-align: left;
        padding: 0 10px;
    }
    .kongtou{
        position: absolute;
        bottom: 360px;
        right: 250px;
    }
    .kongtou>img{
        width: 150px;
        height: 150px;
    }
    .choice_now_price{
        color: white;
        display: block;
        margin-bottom: 10px;
    }
</style>



