<template>
    <div class="box">
        <div>
            <h4>设置奖项</h4>
            <ul class="price_in">
                <li>奖项名称：<input :class="{worin:is_worin}" type="text" v-model='price_name'></li>
                <li>奖项个数：<input :class="{worin:is_worin}" type="text" v-model='price_num'></li>
                <li><button @click='save'>保存</button></li>
            </ul>
        </div>
        <table class="show_price">
            <tr><th>奖项</th><th>个数</th><th>操作</th></tr>
            <tr v-for="(item,index) in price_list" :key="index"><td>{{item.name}}</td><td>{{item.num}}</td><td><span @click="edit(index)">修改</span><span @click="remove(index)">删除</span></td></tr>
        </table>
        <div class="set_title">
            <h4>设置抽奖主办方名字(显示在主页上面的标题)</h4>
            <input type="text" v-model="set_title">
            <button @click='save_title'>保存</button>
        </div>
        <span class="super_save"><router-link to="/set" target="_blank">超级设置</router-link></span>
    </div>
</template>
<script>
    export default{
        data(){
            return{
                price_name:'',
                price_num:'',
                price_list:[],
                is_worin:false,
                set_title:'',
                index:''
                
            }
        },
        mounted(){
            var s=JSON.parse(localStorage.getItem("n_set"));
            if(s==null){
                this.price_list=[];
            }else{
                this.price_list=s;
            }
        },
        methods:{
            save(){
                if(this.price_name!=''&&this.price_num!=""){
                    var i=this.index;
                    if(this.price_list[i].name!=""){
                           this.price_list[i].name=this.price_name;
                           this.price_list[i].num=this.price_num;
                       }else{
                           this.price_list.push({
                                name:this.price_name,
                                num:this.price_num
                            });
                       }
                      localStorage.setItem('n_set',JSON.stringify(this.price_list))
                      this.price_name='';
                      this.price_num="";
                      this.index='';
                   }else{
                        this.is_worin=true;
                   }
                
                
            },
            edit(index){
                var obj=this.price_list.slice(index,index+1);
                console.log(index,this.price_list,obj)
                this.price_name=obj[0].name;
                this.price_num=obj[0].num;
                this.index=index;
            },
            remove(index){
                this.price_list.splice(index,1);
                localStorage.setItem('n_set',JSON.stringify(this.price_list))
            },
            save_title(){
                localStorage.setItem('title',JSON.stringify(this.set_title))
            }
        }
    }





</script>
<style scoped="true">
    .worin{
        border: 1px sloid red;
    }
    .box{
        width: 700px;
        margin: auto;
        margin-top: 50px;
    }
    .price_in{
        height: 50px;
    }
    .price_in>li{
        float: left;
        margin-top: 30px;
        margin-right: 25px;
    }
    .super_save{
        display: block;
        padding-top: 50px;
        text-align: center;
        clear: both;
    }
    .super_save>a{
        background-color: dodgerblue;
        display: inline-block;
        padding: 5px 7px;
        color: white;
        border-radius: 4px;
    }
    .show_price{
        clear: both;
        width: 500px;
        border-collapse: collapse;
        margin-top: 30px;
        margin-left: 30px;
    }
    .show_price td,.show_price th{
        border: 1px solid black;
        width: 100px;
        height: 25px;
    }
    .show_price span{
        display: inline-block;
        margin-right: 10px;
        cursor: pointer;
    }
    .show_price span:nth-child(1){
        color: blue;
    }
    .show_price span:nth-child(2){
        color: red;
    }
    .set_title{
        margin-top: 30px;
    }
    .set_title>h4{
        margin-bottom: 15px;
    }
</style>


