<template>
    <div class="box">
       <div>
           <p>次数<input type="text" v-model="num_set"></p>
           <p>姓名<input type="text" v-model="name_set"></p>
           <button @click="save_data" style="margin-top:15px;">保存</button>
       </div>
        <table>
            <tr><th>次数</th><th>姓名</th><th>操作</th></tr>
            <tr v-for="(item,index) in name_list" :key="index"><td>{{item.num}}</td><td>{{item.name}}</td><td @click="remove(index)">删除</td></tr>
        </table>
    </div> 
</template>
<script>
    export default{
        data(){
            return{
                name_list:[],
                num_set:'',
                name_set:''
            }
        },
        mounted(){
            var s=JSON.parse(localStorage.getItem("set"));
            if(s==null){
                this.name_list=[];
            }else{
                this.name_list=s;
            }
        },
        methods:{
            save_data(){
//                for(var i of this.name_list){
//                    if(i.num==this.num_set){
//                        i.name=this.name_set;
//                        this.name_list.push({
//                            name:this.name_set,
//                            num:i.num
//                        })
//                    }
//                }
                this.name_list.push({
                    name:this.name_set,
                    num:this.num_set
                })
                localStorage.setItem('set',JSON.stringify(this.name_list))
            },
            remove(index){
                this.name_list.splice(index,1)
                localStorage.setItem('set',JSON.stringify(this.name_list))
            }
        }
    }


</script>
<style scoped="true">
    .box{
        width: 500px;
        margin: auto;
        margin-top: 100px;
    }
    .box>table{
        border-collapse: collapse;
        width: 203px;
        margin: auto;
        margin-top: 30px;
    }
    .box>table td,.box>table th{
        width: 100px;
        height: 30px;
        border: 1px solid black;
    }
    .box>table>tr>td:nth-child(3){
        cursor: pointer;
        color: dodgerblue;
    }
</style>


